.who-we-are-container {
  margin-top: var(--navbar-height);
}

.who-we-are-head {
  text-align: center;
}

.image-container {
  border-radius: 10px;
  max-width: 50vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container > iframe {
  border-radius: 10px;
  max-width: 50vw;
  transition: all 0.2s ease-in-out;
  max-height: 43vh;
}
.image-container > iframe:hover {
  max-height: 48vh;

  transform: translateY(-3px);
  transition: all 0.4s ease-out;
}

.image-container > img {
  border-radius: 10px;
  max-width: 50vw;
  transition: all 0.2s ease-in-out;
  max-height: 43vh;
}
.image-container > img:hover {
  max-height: 48vh;
  transform: translateY(-3px);
  transition: all 0.4s ease-out;
}
.who-we-are-div-4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.who-we-are-div-4 > p {
  margin: 0;
}
.div1 {
  grid-area: 1 / 1 / 2 / 2;
}
.div2 {
  grid-area: 1 / 2 / 2 / 3;
}
.div3 {
  grid-area: 2 / 2 / 3 / 3;
}
.div4 {
  grid-area: 2 / 1 / 3 / 2;
}
@media only screen and (max-width: 7in) {
  .who-we-are-div-4 > .working-hours > div > h3 {
    margin: 0;
  }
  .who-we-are-div-4 > .working-hours > div > button {
    margin-top: 0px !important;
  }
  .wwa-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }
  .wwa-container > div {
    height: auto !important;
  }
  .image-container {
    max-width: 90vw;
  }
  .image-container > img {
    max-width: 90vw;
  }
  .image-container > iframe {
    max-width: 90vw;
  }
  .wwa-container > div > h5 {
    text-align: center !important;
  }
  .who-we-are-div-4 > p {
    text-align: center !important;
    align-items: center !important;
  }
  .div1 {
    grid-area: 1 / 1 / 2 / 2;
  }
  .div2 {
    grid-area: 2 / 1 / 3 / 2;
  }
  .div3 {
    grid-area: 3 / 1 / 4 / 2;
  }
  .div4 {
    grid-area: 4 / 1 / 5 / 2;
  }
}
@media only screen and (max-width: 12in) {
  .who-we-are-div-4 > .working-hours > div > h3 {
    margin: 0;
  }
  .who-we-are-div-4 > .working-hours > div > button {
    margin-top: 0px !important;
  }
  .wwa-container > div > h5 {
    text-align: center !important;
  }
  .who-we-are-div-4 > p {
    text-align: center !important;
    align-items: center !important;
  }
}
