.top-part {
  display: flex;
  justify-content: center;
  opacity: 0;
  transition: opacity 1s ease;
}
.top-image {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.loaded {
  opacity: 1;
}

.top-part-text {
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: 9;
  background-color: #0000005b;
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
}
