.truck-slider {
  position: absolute;
  right: 100vw !important;
}
.truck-slider.showing {
  right: -60vw !important;
}
.top-text {
  padding: 40px;
  color: #ffffff;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.697);
  margin: auto;
  font-size: 4rem;
  font-weight: bolder;
  transform: translateX(-120%);
  transition: transform 1s ease-in-out;
  text-align: center;
  margin: 0;
}
.top-text > h3 {
  margin: 0;
}
.top-text > div {
  margin-top: 10%;
}
.top-text > div > p {
  font-size: 2rem;
}
.top-text.show {
  transform: translateX(0);
}
.fade-out {
  opacity: 0;
  transition: opacity 0.4s ease-out;
}
.fade-in {
  opacity: 1;
  transition: opacity 0.4s ease-out;
}
.fade-out-img {
  opacity: 0.3;
  transition: opacity 0.4s ease-out;
}
.fade-in-img {
  opacity: 1;
  transition: opacity 0.4s ease-out;
}
.p-services {
  font-size: 1rem;
  margin-top: 20px;
  font-style: oblique;
}
.services {
  width: 96vw;
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
}
.service {
  max-height: 400px;
  height: 350px;
  width: 30vw;
  padding: 3%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 10px;
  background: rgb(239, 239, 239);
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
}
.service:hover {
  background-color: #c8c8c85b;
  transform: translateY(-3px);
  transition: all 0.3s ease-out;
}
.service > h7 {
  font-size: 2rem;
}
#services-container {
  text-align: center;
}
@media only screen and (max-width: 12in) {
  .top-text > h3 {
    font-size: xx-large;
    margin: 0;
  }
  .top-text > div > h6 {
    font-size: large;
  }
  .top-text > div > p {
    font-size: 1rem;
  }
  .logo-home {
    width: 26% !important;
  }
  .services {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .service {
    max-width: 90%;
    width: 350px;
  }
  .truck-slider.showing {
    right: -70vw !important;
  }
  #truck-svg {
    width: 250px;
  }
}
