#content {
  margin-top: calc(var(--navbar-height));
  padding: 2% 0;
}
.page-header {
  text-align: center;
  padding: 1%;
}
.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - var(--navbar-height)-10px);
}
.map {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
@media only screen and (max-width: 12in) {
  .page-container {
    display: grid !important;
    grid-template-columns: 1fr !important;
    justify-content: center;
    align-items: center;
  }
  .side-expl{
    display: grid;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
  }
  .contact-us-link{
    display: grid;
    text-decoration: none;
    margin-top: 5%;
  }
}
